:root {
  --background: #ffffff;
  --color-background: var(--background);
  --color-foreground: var(--foreground);
  --font-montserrat: var(--font-montserrat);
  --font-space-grotesk: var(--font-space-grotesk);
  --button-bg: rgba(70, 184, 134, 1);
  --ease-out-expo: cubic-bezier(0.16, 1, 0.3, 1);
  --ease-out-quart: cubic-bezier(0.25, 1, 0.5, 1);
  --ease-in-out-cubic: cubic-bezier(0.65, 0, 0.35, 1);
  --spring: cubic-bezier(0.34, 1.56, 0.64, 1);
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
  }
}
html {
  scroll-behavior: smooth;
}
body {
  font-family: var(--font-space-grotesk), sans-serif;
  min-height: 100vh;
}
.hover-button-bg:hover {
  color: var(--button-bg);
}
.btn-primary {
  position: relative;
  transition: all 0.4s var(--ease-out-expo);
  overflow: hidden;
}
.btn-primary::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.2) 0%, transparent 50%);
  opacity: 0;
  transition: opacity 0.4s var(--ease-out-expo);
}
.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px -10px rgba(70, 184, 134, 0.5);
}
.btn-primary:hover::before {
  opacity: 1;
}
.btn-primary:active {
  transform: translateY(0) scale(0.98);
}
@keyframes marquee {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}
.animate-marquee {
  animation: marquee 25s linear infinite;
}
.animate-marquee:hover {
  animation-play-state: paused;
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeInScale {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
.animate-fade-in-up {
  animation: fadeInUp 0.6s var(--ease-out-expo) forwards;
}
.animate-fade-in-down {
  animation: fadeInDown 0.5s var(--ease-out-expo) forwards;
}
.animate-fade-in {
  animation: fadeIn 0.5s var(--ease-out-expo) forwards;
}
.animate-fade-in-scale {
  animation: fadeInScale 0.5s var(--ease-out-expo) forwards;
}
.delay-100 {
  animation-delay: 100ms;
}
.delay-200 {
  animation-delay: 200ms;
}
.delay-300 {
  animation-delay: 300ms;
}
.delay-400 {
  animation-delay: 400ms;
}
.delay-500 {
  animation-delay: 500ms;
}
@keyframes pulse-soft {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}
@keyframes glow {
  0%,
  100% {
    box-shadow: 0 0 20px rgba(70, 184, 134, 0.3);
  }
  50% {
    box-shadow: 0 0 40px rgba(70, 184, 134, 0.5);
  }
}
.animate-pulse-soft {
  animation: pulse-soft 2s ease-in-out infinite;
}
.animate-glow {
  animation: glow 2s ease-in-out infinite;
}
.card-hover {
  transition: all 0.4s var(--ease-out-expo);
}
.card-hover:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px -15px rgba(0, 0, 0, 0.15);
}
.card-hover-subtle {
  transition: all 0.3s var(--ease-out-quart);
}
.card-hover-subtle:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 24px -8px rgba(0, 0, 0, 0.12);
}
.link-underline {
  position: relative;
}
.link-underline::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background: currentColor;
  transition: width 0.3s var(--ease-out-expo);
}
.link-underline:hover::after {
  width: 100%;
}
.icon-hover {
  transition: transform 0.3s var(--ease-out-expo);
}
.icon-hover:hover {
  transform: scale(1.1);
}
.arrow-hover {
  transition: transform 0.3s var(--ease-out-expo);
}
.group:hover .arrow-hover {
  transform: translateX(4px);
}
@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}
.shimmer {
  background: linear-gradient(90deg,
      transparent 0%,
      rgba(255, 255, 255, 0.1) 50%,
      transparent 100%);
  background-size: 200% 100%;
  animation: shimmer 2s infinite;
}
@keyframes float {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}
.animate-float {
  animation: float 4s ease-in-out infinite;
}
@keyframes scaleBounce {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
.hover-scale-bounce:hover {
  animation: scaleBounce 0.4s var(--spring);
}
.hover-rotate {
  transition: transform 0.5s var(--ease-out-expo);
}
.hover-rotate:hover {
  transform: rotate(180deg);
}
.border-glow {
  position: relative;
}
.border-glow::before {
  content: '';
  position: absolute;
  inset: -2px;
  background: linear-gradient(135deg, #46B886, #1C4A3F, #46B886);
  border-radius: inherit;
  opacity: 0;
  transition: opacity 0.4s var(--ease-out-expo);
  z-index: -1;
}
.border-glow:hover::before {
  opacity: 1;
}
input:focus,
textarea:focus {
  outline: none;
  border-color: #46B886 !important;
  box-shadow: 0 0 0 3px rgba(70, 184, 134, 0.15);
  transition: all 0.3s var(--ease-out-expo);
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}
::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
  transition: background 0.3s ease;
}
::-webkit-scrollbar-thumb:hover {
  background: #a1a1a1;
}
.scrollbar-hide {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.scrollbar-hide::-webkit-scrollbar {
  display: none;
}
* {
  transition-timing-function: var(--ease-out-expo);
}
@supports (backdrop-filter: blur(20px)) {
  .glass {
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
  }
}
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
  .animate-marquee {
    animation: none;
  }
}
